// src/main.js
import { fetchImages } from "./js/pixaay-api.js";
import { createGalleryMarkup, clearGallery, renderGallery } from "./js/render-functions.js";

import SimpleLightbox from "simplelightbox";
import "simplelightbox/dist/simple-lightbox.min.css";

import iziToast from "izitoast";
import "izitoast/dist/css/iziToast.min.css";

// DOM
const form = document.querySelector("#search-form");
const gallery = document.querySelector(".gallery");
const loadMoreBtn = document.querySelector("#load-more");
const loader = document.querySelector(".loader");

// state
let currentQuery = "";
let page = 1;
const PER_PAGE = 150;
let totalHits = 0;

let lightbox = new SimpleLightbox(".gallery a", {
  captionsData: "data-caption",
  captionDelay: 250,
});

// Ініціалізація станів інтерфейсу
hideLoadMore();
hideLoader();

form.addEventListener("submit", async (e) => {
  e.preventDefault();

  const query = form.elements.query.value.trim();

  if (query === "") {
    iziToast.warning({
      title: "Warning",
      message: "Please enter a search query",
      position: "topRight",
    });
    return;
  }

  // новий пошук — скидаємо сторінку, очищаємо галерею і ховаємо Load more
  currentQuery = query;
  page = 1;
  clearGallery(gallery);
  hideLoadMore();

  showLoader();

  try {
    const data = await fetchImages(currentQuery, page, PER_PAGE);
    hideLoader();

    totalHits = data.totalHits || 0;

    if (!data.hits || data.hits.length === 0) {
      iziToast.info({
        title: "No results",
        message:
          "Sorry, there are no images matching your search query. Please try again!",
        position: "topRight",
      });
      return;
    }

    // render first batch
    const markup = createGalleryMarkup(data.hits);
    renderGallery(gallery, markup);

    // refresh lightbox
    lightbox.refresh();

    // show or hide Load more depending on remaining items
    if (page * PER_PAGE < totalHits) {
      showLoadMore();
    } else {
      hideLoadMore();
      iziToast.info({
        title: "End",
        message: "We're sorry, but you've reached the end of search results.",
        position: "topRight",
      });
    }
  } catch (err) {
    hideLoader();
    iziToast.error({
      title: "Error",
      message: "Something went wrong. Please try again later.",
      position: "topRight",
    });
  }
});

// Load more click
loadMoreBtn.addEventListener("click", async () => {
  // збільшуємо сторінку і грузимо наступний пакет
  page += 1;
  hideLoadMore();
  showLoader();

  try {
    const data = await fetchImages(currentQuery, page, PER_PAGE);
    hideLoader();

    if (!data.hits || data.hits.length === 0) {
      iziToast.info({
        title: "No more",
        message: "We're sorry, but you've reached the end of search results.",
        position: "topRight",
      });
      hideLoadMore();
      return;
    }

    const markup = createGalleryMarkup(data.hits);
    renderGallery(gallery, markup);

    // refresh lightbox so new items work
    lightbox.refresh();

    // плавний скрол після додавання
    smoothScrollAfterAppend();

    // показати або сховати кнопку в залежності від того, чи є ще
    if (page * PER_PAGE < data.totalHits) {
      showLoadMore();
    } else {
      hideLoadMore();
      iziToast.info({
        title: "End",
        message: "We're sorry, but you've reached the end of search results.",
        position: "topRight",
      });
    }
  } catch (err) {
    hideLoader();
    iziToast.error({
      title: "Error",
      message: "Something went wrong. Please try again later.",
      position: "topRight",
    });
  }
});

/* ---------- UI Helpers ---------- */

function showLoader() {
  if (!loader) return;
  loader.classList.remove("hidden");
}

function hideLoader() {
  if (!loader) return;
  loader.classList.add("hidden");
}

function showLoadMore() {
  if (!loadMoreBtn) return;
  loadMoreBtn.classList.remove("hidden");
}

function hideLoadMore() {
  if (!loadMoreBtn) return;
  loadMoreBtn.classList.add("hidden");
}

/* ---------- Smooth scroll ---------- */
function smoothScrollAfterAppend() {
  // беремо першу карточку (висота використовуємо як репер)
  const firstCard = gallery.querySelector(".photo-card");
  if (!firstCard) return;

  const { height } = firstCard.getBoundingClientRect();
  // скролимо на дві висоти карточки
  window.scrollBy({
    top: height * 2,
    left: 0,
    behavior: "smooth",
  });
}
